<template>
  <div>
    <Input type="text" v-model="component_id" placeholder="component_id" />
    <Input
      v-if="showInfo"
      type="textarea"
      v-model="content"
      :autosize="{ minRows: 6 }"
      placeholder="content"
    />
    <Button @click="search" v-if="!showInfo"></Button>
    <Button @click="create" v-if="showInfo"></Button>
  </div>
</template>
<script>
import { ajax } from "@/util/ajax";
export default {
  data() {
    return {
      component_id: "",
      content: "",
      showInfo: false
    };
  },
  methods: {
    create() {
      if (this.component_id === "") {
        this.$Message.error("component_id ");
        return;
      }
      // if (this.content === "") {
      //   this.$Message.error("content ");
      //   return;
      // }
      ajax({
        urlKey: "/api/usage/update",
        methods: "POST",
        data: {
          id: this.component_id,
          content: this.content
        }
      }).then(res => {
        console.log(res);
        if (res.status === 1) {
          this.$Message.success("");
        } else {
          this.$Message.error(res.message);
        }
      });
    },
    search() {
      if (this.component_id === "") {
        this.$Message.error("component_id ");
        return;
      }
      ajax({
        urlKey: "/api/usage/get",
        methods: "POST",
        data: {
          id: this.component_id
        }
      }).then(res => {
        if (res.status === 1) {
          this.content = res.data.length ? res.data[0].content : "";
          this.showInfo = true;
        } else {
          this.$Message.error(res.message);
        }
      });
    }
  }
};
</script>
