<template>
  <div class="stylelib-tpl">
    <div class="opr">
      <Button @click="createCate" type="primary"></Button>
      <Select v-model="search.keyType" style="width: 160px" @on-change="searchTpl">
        <Option v-for="(i, j) in searchCate" :key="j" :value="i.id+''">{{ i.name }}</Option>
      </Select>
    </div>
    <Table border :loading="loading" :columns="cateColumns" :data="cateList">
      <template slot-scope="{ row }" slot="time">
        <span>{{ row.created_at | moment }}</span>
      </template>
    </Table>

    <!--  -->
    <Modal v-model="update" title="" footer-hide>
      <Form ref="cateValidate" :model="cateValidate" :rules="cateRuleValidate" :label-width="80">
        <FormItem label="" prop="title">
          <Input v-model="cateValidate.title" placeholder="" />
        </FormItem>
        <FormItem label="" prop="tag_id">
          <Select v-model="cateValidate.tag_id" placeholder="">
            <Option v-for="(i, j) in cate" :key="j" :value="i.id+''">{{ i.name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="" prop="desc">
          <Input v-model="cateValidate.desc" placeholder="" />
        </FormItem>
        <FormItem label="KEY" prop="cover">
          <Input v-model="cateValidate.cover" placeholder="KEY" />
        </FormItem>
        <FormItem label="" prop="url">
          <Input v-model="cateValidate.url" placeholder="" />
        </FormItem>
        <FormItem label="" prop="preview">
          <Input v-model="cateValidate.preview" placeholder="" />
        </FormItem>
        <FormItem>
          <Button type="primary" @click="handleCate('cateValidate')">{{ isCreate ? "" : "" }}</Button>
          <!-- <Button @click="handleReset('formValidate')" style="margin-left: 8px">Reset</Button> -->
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
import { ajax } from "@/util/ajax";
export default {
  props: {
    cate: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    searchCate() {
      return [{ name: "", id: "0" }, ...this.cate];
    }
  },
  data() {
    return {
      update: false,
      loading: false,
      // searchCate: [],
      search: {
        keyWord: "",
        keyType: "0"
      },
      cateColumns: [
        {
          title: "",
          key: "title",
          render: (h, params) => {
            return h("div", [h("strong", params.row.title)]);
          }
        },
        {
          title: "",
          key: "desc"
        },
        {
          title: "KEY",
          key: "cover"
        },
        {
          title: "",
          key: "url"
        },
        {
          title: "",
          key: "preview"
        },
        {
          title: "",
          key: "hot"
        },
        {
          title: "",
          slot: "time"
          // key: "created_at",
          // render: (h, params) => {
          //   return h("div", [h("strong", params.row.created_at)]);
          // }
        },
        {
          title: "",
          key: "action",
          width: 150,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.editCate(params.row);
                    }
                  }
                },
                ""
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click: () => {
                      this.removeCate(params.row);
                    }
                  }
                },
                ""
              )
            ]);
          }
        }
      ],
      cateList: [],
      cateValidate: {
        title: "",
        tag_id: "",
        desc: "",
        cover: "",
        url: "",
        preview: ""
      },
      cateRuleValidate: {
        title: [
          {
            required: true,
            message: "10",
            trigger: "blur",
            max: 10
          }
        ],
        tag_id: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        desc: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        cover: [
          {
            required: true,
            message: "KEY",
            trigger: "blur"
          }
        ],
        url: [
          {
            required: true,
            message: "url",
            trigger: "blur",
            type: "url"
          }
        ],
        preview: [
          {
            required: true,
            message: "url",
            trigger: "blur",
            type: "url"
          }
        ]
      },
      isCreate: false
    };
  },
  created() {
    this.getCate();
  },
  methods: {
    // 
    async getCate(tag_id) {
      if (tag_id === "0") tag_id = 0;
      this.loading = true;
      let res = await ajax({
        urlKey: "/api/template/item/list",
        methods: "POST",
        data: {
          tag_id: tag_id
        }
      });
      if (res && res.status == 1) {
        this.cateList = res.data;
      } else {
        this.$Message.error(res.data);
      }
      this.loading = false;
    },
    editCate(row) {
      this.isCreate = false;
      this.cateValidate = { ...row };
      this.update = true;
    },
    removeCate(row) {
      this.$Modal.confirm({
        title: "",
        content: `<p>,${row.title}?</p>`,
        onOk: () => {
          ajax({
            urlKey: "/api/template/item/delete",
            methods: "POST",
            data: {
              template_id: row.id
            }
          }).then(res => {
            if (res.status === 1) {
              this.$Message.success("!");
              this.getCate();
            } else {
              this.$Message.error(res.msg);
            }
          });
        },
        onCancel: () => {}
      });
    },
    createCate() {
      this.$refs.cateValidate.resetFields();
      this.isCreate = true;
      this.update = true;
    },
    handleCate(name) {
      let fd = {},
        url = "/api/template/item/create";
      fd = { ...this.cateValidate };
      if (!this.isCreate) {
        fd.template_id = this.cateValidate.id;
        url = "/api/template/item/update";
      }
      this.$refs[name].validate(valid => {
        if (valid) {
          ajax({
            urlKey: url,
            methods: "POST",
            data: fd
          }).then(res => {
            if (res.status === 1) {
              this.$Message.success("Success!");
              this.update = false;
              this.getCate();
            } else {
              this.$Message.error(res.msg);
            }
          });
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
    searchTpl() {
      this.getCate(this.search.keyType);
    }
  }
};
</script>

<style lang="less" scoped>
.stylelib-tpl {
  .opr {
    height: 60px;
    line-height: 60px;
    button {
      margin-right: 20px;
    }
  }
}
</style>
